import React, { useState } from 'react';
import { Box, Typography, Container, Button, TextField, Snackbar, Grid, Avatar } from '@mui/material';
import Stock from './Stock';
import { useNavigate } from 'react-router-dom';
import foto from './/IMG_20221031_211846.jpg'

function App() {
  const [usuario, setUsuario] = useState('');
  const [contrasea, setContrasea] = useState('');
  const [usuarioLogueado, setUsuarioLogueado] = useState(false);
  const [mensaje, setMensaje] = useState('');
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const navigate = useNavigate();

  // Lista de mensajes aleatorios para el inicio de sesin
  const mensajesBienvenida = [
    'Deberas caminar ms, te noto ms gordito!',
    'Dormir la siesta es bueno.',
    'Recuerda comprar sahumerios para Pablo.',
    'Recuerda llamar a Ale.',
    'Buenos das, hoy va a llover.',
  ];

  // Funcin para obtener un mensaje aleatorio
  const obtenerMensajeAleatorio = () => {
    const indiceAleatorio = Math.floor(Math.random() * mensajesBienvenida.length);
    return mensajesBienvenida[indiceAleatorio];
  };

  const handleLogin = () => {
    if (usuario === 'ruben' && contrasea === 'andu1234') {
      setUsuarioLogueado(true);
      setMensaje(obtenerMensajeAleatorio());  // Establece un mensaje aleatorio al iniciar sesin
    } else {
      setMensaje('Usuario o contrasea incorrectos');
    }
    setSnackbarOpen(true);
  };

  const handleLogout = () => {
    setUsuarioLogueado(false);
    setUsuario('');
    setContrasea('');
    setMensaje('Sesin cerrada');
    setSnackbarOpen(true);
  };

  const goToEstadisticas = () => {
    navigate('/Estadisticas');
  };

  const goToVentas = () => {
    navigate('/Ventas');
  };

  const handleKeyDown = (event) => {
    if (event.key === 'Enter') {
      handleLogin();
    }
  };

  return (
    <Container maxWidth="lg">
      <Box sx={{ display: 'flex', justifyContent: 'center', flexDirection: 'column', mb: 10, mt: 5 }}>
        {/* Cambiar la tipografa del ttulo "Lumar" */}
        <Typography
          variant="h2"
          textAlign="left"
          color='lightBlue'
          sx={{ mb: 3, fontFamily: '"Comic Sans MS", cursive, sans-serif', fontWeight: 'bold' }}
        >
          Lumar
        </Typography>

        {/* Seccin de login */}
        <Grid container spacing={2} justifyContent="center">
          {!usuarioLogueado ? (
            <Grid item xs={12} sm={8} md={6} lg={4}>
              <Typography variant="h6" sx={{ mb: 2 }}>Iniciar sesin para acceder al sistema</Typography>
              <TextField
                label="Usuario"
                fullWidth
                variant="outlined"
                sx={{ mb: 2 }}
                value={usuario}
                onChange={(e) => setUsuario(e.target.value)}
                onKeyDown={handleKeyDown} // Detectar tecla Enter
              />
              <TextField
                label="Contrasea"
                type="password"
                fullWidth
                variant="outlined"
                sx={{ mb: 2 }}
                value={contrasea}
                onChange={(e) => setContrasea(e.target.value)}
                onKeyDown={handleKeyDown} // Detectar tecla Enter
              />
              <Button
                onClick={handleLogin}
                variant="contained"
                color="primary"
                fullWidth
              >
                Iniciar sesin
              </Button>
            </Grid>
          ) : (
            <Grid item xs={12} sm={8} md={6} lg={4}>
              <Typography variant="h5" sx={{ mb: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                {/* Avatar al lado del texto */}
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  <Avatar sx={{ width: 40, height: 40, mr: 2 }} alt="Rubn" src={foto} />
                  <Typography>Bienvenido, Rubn</Typography>
                </Box>
                <Typography variant="h6" sx={{ ml: 2, fontStyle: 'italic', color: 'gray' }}>
                  {mensaje}
                </Typography>
              </Typography>
              <Button
                onClick={handleLogout}
                variant="contained"
                color="warning"
                fullWidth
                sx={{ mb: 2 }}
              >
                Cerrar sesin
              </Button>

              <Button
                onClick={goToEstadisticas}
                variant="contained"
                color="secondary"
                fullWidth
                sx={{ mb: 2 }}
              >
                Ir a Estadsticas
              </Button>
            </Grid>
          )}
        </Grid>

        {/* Botn de acceso a ventas */}
        <Button
          onClick={goToVentas}
          variant="contained"
          color="success"
          fullWidth
          sx={{ mt: 2 }}
        >
          Ir a Ventas
        </Button>

        {/* Si el usuario est logueado, mostrar el acceso a Stock */}
        {usuarioLogueado && (
          <Box sx={{ mt: 4 }}>
            <Typography variant="h6">
              Acceso al Stock:
            </Typography>
            <Stock />
          </Box>
        )}

        <Snackbar
          open={snackbarOpen}
          autoHideDuration={4000}
          onClose={() => setSnackbarOpen(false)}
          message={mensaje}
        />
      </Box>
    </Container>
  );
}

export default App;
